import {
    trigger,
    transition,
    style,
    query,
    animate,
  } from '@angular/animations';


  export const openCloseAnimation =
    trigger('openClose', [
        transition('* <=> *', [

        query(':enter, :leave', [
            style({
            position: 'absolute',
            left: 0,
            width: '100%',
            opacity: 0,
            height: '100%',
            transform: 'scale(0) opacity(0)',

            }),
        ],
        { optional: true }),

        query(':enter', [
            animate('800ms ease', style({ opacity: 1, transform: 'scale(1) opacity(1)' })),
        ],
        { optional: true })
        ]),
    ]);

    export const fadeAnimation =

    trigger('fadeAnimation', [

        transition( '* => *', [

            query(':leave',
                [
                    style({
                        position: 'relative',
                        left: 0,
                        opacity: 1,
                        height: '100vh',
                        width: '100vw'
                     }),
                    animate('0.2s', style({ opacity: 0, transform: 'scale(0) opacity(0)' }))
                ],
                { optional: true }
            ),

            query(':enter',
                [
                    style({
                        position: 'relative',
                        left: 0,
                        opacity: 0,
                        height: '100vh',
                        width: '100vw'
                    }),
                    animate('0.2s', style({ opacity: 1, transform: 'scale(1) opacity(1)' }))
                ],
                { optional: true }
            )

        ])

    ]);
